<template>
    <div class="bgbox">
         <audio id="audio" :src="music" ref="audio"  @pause="pause" @play="play">
         </audio>
         <div class="songname"><h2>{{num}} · {{songername}}</h2></div>
         <div class="fourbtn">
             <ul>
                 <li><span class="iconfont icon-main_girl"></span></li>
                 <li><span class="iconfont icon-xiazai"></span></li>
                 <li><router-link to="/comment"><span class="iconfont icon-pinglun1"></span></router-link></li>
                 <li><span class="iconfont icon-gengduo"></span></li>
             </ul>
         </div>
         <div class="jdtbox">
             <p class="startnum" :max="duration">{{second/60 > 9 ? parseInt(second/60) : '0'+parseInt(second/60) }}:{{second%60 >= 10 ? parseInt(second%60) : '0'+parseInt(second%60)}}</p>
             <el-progress :percentage="progressing_audio" status="success" :stroke-width="2"  style="margin-left:1.3rem;float:left;width:6.14rem;"></el-progress>		
			 <p class="alltime">{{alltime}}</p>
	    </div>
        <div class="fivebtn">
            <ul>
                 <li><span class="iconfont icon-suijibofang"></span></li>
                 <li><span class="iconfont icon-shangyiqu" @click="on(num)"></span></li>
                 <li class="centerli"><span class="iconfont" :class="isPlaying" @click="togglePlay"></span></li>
                 <li><span class="iconfont icon-xiayiqu" @click="down(num)"></span></li>
                 <li><span class="iconfont icon-bofangliebiao"></span></li>
             </ul>
        </div>
    </div>
</template>

<script>
import { watch } from 'fs';
import song1 from '@/assets/style/mp3/red.mp3';
import song2 from '@/assets/style/mp3/frx.mp3';
import song3 from '@/assets/style/mp3/qlx.mp3';
import song4 from '@/assets/style/mp3/yq.mp3';
import song5 from '@/assets/style/mp3/Planet.mp3';
import bgimg1 from '@/assets/style/images/musicplayerbgimg.png';
import bgimg2 from '@/assets/style/images/musicplayerbgimg2.png';
import bgimg3 from '@/assets/style/images/musicplayerbgimg3.png';
import bgimg4 from '@/assets/style/images/musicplayerbgimg4.png';
import bgimg5 from '@/assets/style/images/musicplayerbgimg5.png';
import img1 from '@/assets/style/images/timg.jpg';
import img2 from '@/assets/style/images/timg2.png';
import img3 from '@/assets/style/images/timg3.png';
import img4 from '@/assets/style/images/timg4.png';
import img5 from '@/assets/style/images/timg5.png';
import plimg1 from '@/assets/style/images/comment_01.jpg';
import plimg2 from '@/assets/style/images/comment_02.jpg';
import plimg3 from '@/assets/style/images/comment_03.jpg';
import plimg4 from '@/assets/style/images/comment_04.jpg';
import plimg5 from '@/assets/style/images/comment_05.jpg';
import smimg1 from '@/assets/style/images/home-player_01.png';
import smimg2 from '@/assets/style/images/home-player_02.png';
import smimg3 from '@/assets/style/images/home-player_03.png';
import smimg4 from '@/assets/style/images/home-player_04.png';
import smimg5 from '@/assets/style/images/home-player_05.png';
//import audioStart from '@/assets/style/images/home-player_06.png';
export default {
    props: {
			params: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			}
		},
    data(){
           return{
               playing: false,
               currentTime:'', // 当前播放到的时间
               alltime:'',
               duration:0, // 当前歌曲总时间
               second: 0,
               progressing_audio:0,//进度条百分比
               audioPlayShow: true,
               timer: "" ,
               num:1,
           };
    },
    mounted () {
    //   this.music=song1
    //   this.$store.state.songername='Taylor Swift';
    //this.$store.state.bgimg=bgimg1;
    //   this.$store.state.songimg=img1;
    //   this.$store.state.plimgs=plimg1;
      var audio = document.getElementById('audio')
      audio.addEventListener('loadedmetadata', () => {
      //this.alltime = audio.duration//获取总时长
        let x=audio.duration;
	    var durationq = parseInt(x);
        var minute = parseInt(durationq/60);
        var sec = durationq%60+'';
        var isM0 = ':';
        if(minute < 0){
            minute = '00';
        }else if(minute < 10 ){
            minute = '0'+minute;
        }
        if(sec<10){
            sec = '0'+sec;
        }
        this.alltime=minute+isM0+sec;
      })
        //this.$refs.barColor.style.cssText = 'width:' + 100*this.currentTime / this.alltime+ '%';
 

    },
    methods:{
        play() {
           this.playing = true;
        },  
        pause() {
           this.playing = false
        },
        on(num) {
            num--
            if(num<=0){
               num=5
            }
            switch(num){
                case 5:
                this.$store.state.music=song5;
                this.$store.state.songername='ラムジ';
                this.$store.state.songname='PLANET';
                this.$store.state.bgimg=bgimg5;
                this.$store.state.songimg=img5;
                this.$store.state.plimgs=plimg5;
                this.$store.state.smimgs=smimg5;
                break;

                case 4:
                this.$store.state.music=song4;
                this.$store.state.songername='周杰伦';
                this.$store.state.songname='夜曲';
                this.$store.state.bgimg=bgimg4;
                this.$store.state.songimg=img4;
                this.$store.state.plimgs=plimg4;
                this.$store.state.smimgs=smimg4;
                break;

                case 3:
                this.$store.state.music=song3;
                this.$store.state.songername='周杰伦';
                this.$store.state.songname='七里香';
                this.$store.state.bgimg=bgimg3;
                this.$store.state.songimg=img3;
                this.$store.state.plimgs=plimg3;
                this.$store.state.smimgs=smimg3;
                break;

                case 2:
                this.$store.state.music=song2;
                this.$store.state.songername='周杰伦';
                this.$store.state.songname='发如雪';
                this.$store.state.bgimg=bgimg2;
                this.$store.state.songimg=img2;
                this.$store.state.plimgs=plimg2;
                this.$store.state.smimgs=smimg2;
                break;

                case 1:
                this.$store.state.music=song1;
                this.$store.state.songername='Taylor Swift';
                this.$store.state.songname='Red';
                this.$store.state.bgimg=bgimg1;
                this.$store.state.songimg=img1;
                this.$store.state.smimgs=smimg1;
                break;
            }
            //console.info(num);
           var audio = document.getElementById('audio')
       audio.addEventListener('loadedmetadata', () => {
      //this.alltime = audio.duration//获取总时长
        let x=audio.duration;
	    var durationq = parseInt(x);
        var minute = parseInt(durationq/60);
        var sec = durationq%60+'';
        var isM0 = ':';
        if(minute < 0){
            minute = '00';
        }else if(minute < 10 ){
            minute = '0'+minute;
        }
        if(sec<10){
            sec = '0'+sec;
        }
        this.alltime=minute+isM0+sec;
        this.num=num;
        this.second = 0;
        this.progressing_audio = 0;
        audio.play() ;
      })
           
        },
        down(num) {
            // let i=num
            // i++
            num++
            if(num>=6){
                num=1
            }
            switch(num){
                case 5:
                this.$store.state.music=song5;
                this.$store.state.songername='ラムジ';
                this.$store.state.songname='PLANET';
                this.$store.state.bgimg=bgimg5;
                this.$store.state.songimg=img5;
                this.$store.state.plimgs=plimg5;
                this.$store.state.smimgs=smimg5;
                break;

                case 4:
                this.$store.state.music=song4;
                this.$store.state.songername='周杰伦';
                this.$store.state.songname='夜曲';
                this.$store.state.bgimg=bgimg4;
                this.$store.state.songimg=img4;
                this.$store.state.plimgs=plimg4;
                this.$store.state.smimgs=smimg4;
                break;

                case 3:
                this.$store.state.music=song3;
                this.$store.state.songername='周杰伦';
                this.$store.state.songname='七里香';
                this.$store.state.bgimg=bgimg3;
                this.$store.state.songimg=img3;
                this.$store.state.plimgs=plimg3;
                this.$store.state.smimgs=smimg3;
                break;

                case 2:
                this.$store.state.music=song2;
                this.$store.state.songername='周杰伦';
                this.$store.state.songname='发如雪';
                this.$store.state.bgimg=bgimg2;
                this.$store.state.songimg=img2;
                this.$store.state.plimgs=plimg2;
                this.$store.state.smimgs=smimg2;
                break;

                case 1:
                this.$store.state.music=song1;
                this.$store.state.songername='Taylor Swift';
                this.$store.state.songname='Red';
                this.$store.state.bgimg=bgimg1;
                this.$store.state.songimg=img1;
                this.$store.state.smimgs=smimg1;
                break;
            }
            //console.info(num);
           var audio = document.getElementById('audio')
           audio.addEventListener('loadedmetadata', () => {
      //this.alltime = audio.duration//获取总时长
        let x=audio.duration;
	    var durationq = parseInt(x);
        var minute = parseInt(durationq/60);
        var sec = durationq%60+'';
        var isM0 = ':';
        if(minute < 0){
            minute = '00';
        }else if(minute < 10 ){
            minute = '0'+minute;
        }
        if(sec<10){
            sec = '0'+sec;
        }
        this.alltime=minute+isM0+sec;
        this.num=num;
        this.second = 0;
        this.progressing_audio = 0;
        audio.play() ;
      })
           
        }, 
        togglePlay() {
        if ( !this.params){
	    			return false 
	    		} else {
		    		//console.log(this.params);
		    		this.music = this.params.url;
		    		let audio0 = document.getElementById('audio');
		    		let leng = audio0.duration;
		    		//console.log(audio0.duratuion)
		    		if ( this.audioPlayShow ){
		    		 	 audio0.play() ; 
		    		 	
		    		 	 this.audioPlayShow = false ;
		    		 	 this.timer = setInterval( () => {
		    		 	 	//console.log(1/leng);
		    		 	 	this.progressing_audio = (this.progressing_audio/100 + 0.1/leng)*100 ;
		    		 	 	this.second = this.second + 0.1 ; 
		    		 	 	if (this.progressing_audio >= 100 ) {
		    		 	 		clearInterval(this.timer);
		    		 	 		this.progressing_audio = 0; 
                                  this.second = 0 ;

		    		 	 	}
		    		 	 	//console.log(this.progressing_audio);
				　　　　　}, 100)     		 	 
		    		} else {
		    		 	 audio0.pause()
		    		 	 this.audioPlayShow = true ;
		    		 	 clearInterval(this.timer);
		    		}
	    		}
       },
    },
    computed: {
        isPlaying() {   
         return this.playing ? 'icon-bofang1' : 'icon-zanting1';
        },
        count () {
         return this.$store.state.count
        },
        songername () {
         return this.$store.state.songername
        },
        plimgs () {
         return this.$store.state.plimgs
        },
        music () {
         return this.$store.state.music
        },

    }, 
      




}
    
</script>
<style lang="stylus" scoped>


.songname{
    width 100%
    text-align center
    margin-top 0
}
.songname h2{
    color white
    font-size 20px
}
.fourbtn{
    margin-left 3%
    margin-top 10%
}
.fourbtn ul li{
   display inline-block
   color white
   margin-left 12% 
}
.fourbtn ul li span{
   color white
}
.fivebtn{
    width 100%
    height 100px
    background-color rgba(255,255,255,0.3)
    margin-top 10%
}
.fivebtn ul{
    margin-left 2.6%
}
.fivebtn ul li{
   float left
   color white
   margin-left 8%
   margin-top 8%
}
.fivebtn ul .centerli{
   margin-top 10px 
}
.fivebtn ul li span{
   font-size 28px
}
.fourbtn ul li .icon-main_girl{
    color red
}
.fivebtn ul li .icon-zanting1{
    font-size 80px
}
.fivebtn ul li .icon-bofang1{
    font-size 80px
}
.fourbtn ul li span{
   font-size 28px
}
.jdtbox{margin-top:8%;height 10px}
.jdtbox p{
    color white;
}
.jdtbox .startnum{
    position absolute
    margin-top 0%;
    margin-left 4%
}
.jdtbox .alltime{
    float left
    width 20px
    margin-left -10px
}
.barColor{background:#eeeeee;height: 2px;width: 50%;float: left;border-radius: 20px;}
.barBlock{width: 10px;height: 10px;border-radius: 10px;background:#FFFFFF;position: absolute;left:50%;margin-left: -10px;top: -4px;}
</style>